import * as React from "react";
import {Button, Card, CardContent, TextField, Typography} from "@material-ui/core";
import Http from "../serverApi/http";
import {LoadingComponent} from "./UniversalComponents";

export default class SetAdmin extends React.Component {

    state = {
        adminEmail: '',
        addConfirm: '',
        isLoading: false,
        findUserData: {email: '1', password: '', lastLoginDate: '', registrationDate: '', admin: false}
    };

    public handleAdminEmailChange = (event: any) => {
        this.setState({
            adminEmail: event.target.value
        })
    };

    public getUserData = () => {
        this.setState({
            isLoading: true,
            addConfirm: '',
            findUserData: {email: '1', password: '', lastLoginDate: '', registrationDate: '', admin: false}
        });
        const http = new Http();
        http.getUserDataFromAdmin(this.state.adminEmail, '/getUserDataFromAdmin')
            .then(res => res.json())
            .then(
                (result) => {
                    if (result !== 'false') {
                        this.setState({
                            isLoading: false,
                            findUserData: result
                        })
                    }
                    if (result === 'false') {
                        this.setState({
                            isLoading: false,
                            addConfirm: '  '
                        })
                    }
                }
            )
    }

    public addUserAsAdmin = () => {
        this.setState({
            isLoading: true,
            addConfirm: ''
        });
        const http = new Http();
        http.addUserAsAdmin(this.state.findUserData.email, '/addAdmin')
            .then(res => res.json())
            .then(
                (result) => {
                    if (result === true) {
                        this.setState({
                            addConfirm: '  ',
                            isLoading: false,
                            findUserData: {email: '1', password: '', lastLoginDate: '', registrationDate: '', admin: false}
                        })
                    }
                    if (result === 'false') {
                        this.setState({
                            isLoading: false,
                            addConfirm: '  ',
                        })
                    }
                }
            )
    };

    public removeUserAdmin = () => {
        this.setState({
            isLoading: true,
            addConfirm: ''
        });
        const http = new Http();
        http.removeUserAdmin(this.state.findUserData.email, '/removeUserAdmin')
            .then(res => res.json())
            .then(
                (result) => {
                    if (result === true) {
                        this.setState({
                            addConfirm: '  ',
                            isLoading: false,
                            findUserData: {email: '1', password: '', lastLoginDate: '', registrationDate: '', admin: false}
                        })
                    }
                    if (result === 'false') {
                        this.setState({
                            isLoading: false,
                            addConfirm: '  ',
                        })
                    }
                }
            )
    };

    public deleteAccount = () => {
        this.setState({
            isLoading: true,
            addConfirm: ''
        });
        const http = new Http();
        http.deleteAccount(this.state.findUserData.email, '/deleteAccount')
            .then(res => res.json())
            .then(
                (result) => {
                    if (result === true) {
                        this.setState({
                            addConfirm: ' ',
                            isLoading: false,
                            findUserData: {email: '1', password: '', lastLoginDate: '', registrationDate: '', admin: false}
                        })
                    }
                    if (result === 'false') {
                        this.setState({
                            isLoading: false,
                            addConfirm: '  ',
                        })
                    }
                }
            )
    }


    public render() {

        const userData = (
            <div style={{marginTop: '1rem'}}>
                <Card>
                    <CardContent>
                        <Typography>Email: {this.state.findUserData.email}</Typography>
                        <Typography> : {this.state.findUserData.registrationDate}</Typography>
                        <Typography>  : {this.state.findUserData.lastLoginDate}</Typography>
                        <Typography>: {this.state.findUserData.admin ? <Typography variant="button"></Typography> : <Typography variant="button"></Typography>}</Typography>
                        {this.state.findUserData.admin ?   <Button color="primary" variant="contained" onClick={this.removeUserAdmin}>
                              
                        </Button> : <Button color="primary" variant="contained" onClick={this.addUserAsAdmin}>
                              
                        </Button>}

                        <Button variant="contained" onClick={this.deleteAccount} style={{display: 'flex', marginTop: '10px'}}>
                             
                        </Button>
                    </CardContent>
                </Card>
            </div>
        );

        return (
            <div style={{width: '100%'}}>
                <TextField
                    autoFocus
                    margin="dense"
                    id="login"
                    label={<Typography> </Typography>}
                    fullWidth
                    onChange={this.handleAdminEmailChange}
                />
                <Button color="primary" variant="contained" onClick={this.getUserData}>
                    
                </Button>

                {this.state.isLoading ? LoadingComponent : null}
                {<Typography variant="body1">{this.state.addConfirm}</Typography>}
                {this.state.findUserData.email !== '1' ? userData : null}

            </div>
        )
    }
}
